<template>
  <div>
    <select-user :loadDataFn="loadData" :visible.sync="visible" :replaceFields="{
        id: 'businessId',
        name: 'name',
        head: 'avatar'
      }"></select-user>
  </div>
</template>

<script>
import { SelectUser } from '@handday/components'
import { getDepartmentEmps } from '@/api/organization'

export default {
  name: 'SelectUsers',
  components: {
    SelectUser
  },
  data () {
    return {
      loadData: null,
      visible: false,
      cacheOrganize: null
    }
  },
  mounted() {
    setTimeout(() => {
      this.handleAddEmps({
        corpId: 10001,
        departmentId: '46283110463168512',
        parentId: 0,
        name: '指掌天下',
        canDel: null,
        deptType: 1,
        departmentType: null,
        childrens: [],
        scope: {
          title: 'custom'
        },
        key: '0-0'
      })
    })
  },
  methods: {
    /**
     * 添加负责人弹窗开启
     * */
    handleAddEmps (item) {
      this.loadData = () => {
        console.log('=')
        return new Promise((resolve, reject) => {
          getDepartmentEmps({
            departmentId: '46283110463168512',
            corpId: '10001'
          }).then(res => {
            const { data } = res
            resolve({
              list: data.emps,
              select: data.leaders.map(o => o.businessId)
            })
          }).catch(e => {
            reject(e)
          })
        })
      }
      this.visible = true
      this.cacheOrganize = item
    }
  }
}
</script>
